/**

	TABLE OF CONTENTS FOR `ALL UTILITIES`
	---------------------------

*/
/******/
/******/
/* must be placed in the first place */
/******/
/******/
/* base */
/* ====================================================== 
    <!-- Basic -->
/* ====================================================== */
::placeholder {
  color: #E6E6E8;
}

::selection,
::-moz-selection,
::-webkit-selection {
  background: #1f1f1f;
  color: #fff;
}

html,
body {
  width: 100%;
  min-height: 100%;
  /* Use min-height to correctly determine the true height of <div id="app"></div> */
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-size: 16px;
  /* Required, global font size */
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
a {
  margin: 0;
  padding: 0;
}

body {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.71428571429;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Helvetica Neue", Helvetica, "Microsoft YaHei", STXihei, "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
  color: #212121;
  background-color: transparent;
}

body.is-mobile {
  padding-top: 0;
}

p,
pre,
table,
blockquote {
  margin-bottom: 1.421875rem;
}

select {
  max-width: 100%;
}

.wp-block-quote,
blockquote {
  background: #fff;
  border: 1px solid #F8F8F8 !important;
  border-left: 5px solid #6B6BF6 !important;
  padding: 0 1.75rem 1.25rem !important;
  margin-top: 1.421875rem !important;
  line-height: 2;
  font-size: 1rem;
}

.wp-block-quote::before,
blockquote::before {
  color: #6B6BF6;
  content: open-quote;
  font-size: 5.25rem;
  line-height: 0.1;
  margin-right: 0.21875rem;
  vertical-align: -2.055rem;
  font-family: sans-serif;
  content: "\201C";
  /*Unicode for Left Double Quote*/
}

strong {
  font-weight: 600;
}

hr {
  margin: 1.421875rem 0;
  border-color: #DCDCDC;
}

hr.black {
  border-color: rgba(255, 255, 255, 0.1);
}

a img {
  border: none;
}

img {
  max-width: 100%;
}

iframe {
  border: none !important;
}

code {
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 3px;
  display: inline-block;
  margin: 0 .2rem;
  padding: 0 .4rem;
  white-space: nowrap;
  font-family: "Inconsolata", "Monaco", "Consolas", "Andale Mono", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
}

pre {
  background: #fff;
  white-space: pre;
  word-wrap: break-word;
  overflow: auto;
  padding: 0;
  margin: 1rem 0;
}

pre.poemkit-precode {
  white-space: normal;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08);
}

pre.poemkit-precode label {
  font-family: sans-serif;
  font-weight: normal;
  font-size: 0.8125rem;
  position: absolute;
  left: -5px;
  top: 1rem;
  text-align: center;
  width: 60px;
  user-select: none;
  pointer-events: none;
  color: #ddd;
}

pre.poemkit-precode code {
  font-family: "Inconsolata", "Monaco", "Consolas", "Andale Mono", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  display: block;
  margin: 0 0 0 3rem;
  padding: 1rem;
  border-left: 1px solid #d9d9d9;
  overflow-x: auto;
  font-size: 0.8125rem;
  line-height: 1.125rem;
  color: #444;
  background: #fff;
  white-space: pre;
}

pre.poemkit-precode.poemkit-precode--nolebel label {
  display: none;
}

pre.poemkit-precode.poemkit-precode--nolebel code {
  margin: 0;
  border: none;
}

figure {
  position: relative;
}

figure figcaption {
  position: absolute;
  bottom: 0;
  width: 100%;
  max-height: 100%;
  overflow: auto;
  padding: 1.5rem 1rem 1rem;
  color: #fff;
  text-align: center;
  font-size: 0.875rem;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7) 0, rgba(0, 0, 0, 0.3) 70%, transparent);
}

/* ====================================================== 
   <!-- Brand -->                         
/* ====================================================== */
.poemkit-brand {
  float: left;
  font-size: 1.3125rem;
  font-weight: 700;
  text-transform: uppercase;
  white-space: nowrap;
  max-width: 95%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.poemkit-brand img {
  max-height: 50px;
  margin-top: 0.75rem;
}

.poemkit-brand .poemkit-brand__description {
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  white-space: nowrap;
  width: 90%;
  line-height: 1.2;
  font-weight: 100;
  font-size: 0.75rem;
}

.poemkit-brand--mobile {
  display: none;
}

@media all and (max-width: 768px) {
  .poemkit-brand--mobile {
    display: block;
    text-align: center;
  }
  .poemkit-brand--mobile img {
    max-width: 90%;
    max-height: 100px;
    margin: 20px auto;
  }
  .poemkit-brand img {
    margin-bottom: 0.75rem;
  }
}

/* ====================================================== 
   <!-- List -->      
/* ====================================================== */
ul,
ol {
  list-style: none;
  margin-bottom: 1.421875rem;
}

ul > li,
ol > li {
  margin-bottom: 0;
  transition: all 0.3s ease;
}

/* ====================================================== 
    <!-- Helper Classes -->              
/* ====================================================== */
/*
 * 1. Content Block                     
 * 2. Elements after a floating element will flow around it.
 * 3. Shadow Effect
 * 4. Smooth Transition Effect
 * 5. Element Floating
 * 6. Element Alignment
 * 7. Truncate String with Ellipsis
 * 8. Vertically or Horizontally Aligned Blocks
 * 9. Specifies Whether To Clip Content
 * 10. Specifies a Default Height
 * 11. Hidden The Element on Mobile or Desktop Device
 * 12. Tip Bubble
 * 13. Image Hover Overlay Effects
 * 14. Position
 * 15. Element Hidden Style
 * 16. Border Radius
 * 17. Align Wide and Full Classes For Elements
 * 18. HTML Disclosure Summary element 
*/
/* 
 ---------------------------
 1. Content Block
 ---------------------------
 */
.poemkit-relative {
  position: relative;
}

.poemkit-relative--inline {
  display: inline-block;
  position: relative;
  /* Prevent content overflow */
  max-width: 100%;
  /* Behave like most inline-block elements */
  vertical-align: middle;
  /*
	 * Force hardware acceleration without creating a new stacking context
	 *    to fix 1px glitch when combined with overlays and transitions in Webkit
	 */
  -webkit-backface-visibility: hidden;
}

/* Clip child elements */
.poemkit-relative--inline-clip {
  overflow: hidden;
}

/*----*/
.poemkit-display-none {
  display: none !important;
}

.poemkit-display-inline {
  display: inline !important;
}

.poemkit-display-inline-block {
  display: inline-block !important;
}

.poemkit-display-block {
  display: block !important;
}

.poemkit-display-flex {
  display: flex !important;
}

.poemkit-display-inline-flex {
  display: inline-flex !important;
}

/* The breakpoint is the same as bootstrap 4 */
/*
--breakpoint-xs:0;
--breakpoint-sm:576px;
--breakpoint-md:768px;
--breakpoint-lg:992px;
--breakpoint-xl:1200px;
*/
@media all and (max-width: 576px) {
  .poemkit-display-none--sm {
    display: none !important;
  }
  .poemkit-display-inline--sm {
    display: inline !important;
  }
  .poemkit-display-inline-block--sm {
    display: inline-block !important;
  }
  .poemkit-display-block--sm {
    display: block !important;
  }
  .poemkit-display-flex--sm {
    display: flex !important;
  }
  .poemkit-display-inline-flex--sm {
    display: inline-flex !important;
  }
}

@media all and (max-width: 768px) {
  .poemkit-display-none--md {
    display: none !important;
  }
  .poemkit-display-inline--md {
    display: inline !important;
  }
  .poemkit-display-inline-block--md {
    display: inline-block !important;
  }
  .poemkit-display-block--md {
    display: block !important;
  }
  .poemkit-display-flex--md {
    display: flex !important;
  }
  .poemkit-display-inline-flex--md {
    display: inline-flex !important;
  }
}

@media all and (max-width: 992px) {
  .poemkit-display-none--lg {
    display: none !important;
  }
  .poemkit-display-inline--lg {
    display: inline !important;
  }
  .poemkit-display-inline-block--lg {
    display: inline-block !important;
  }
  .poemkit-display-block--lg {
    display: block !important;
  }
  .poemkit-display-flex--lg {
    display: flex !important;
  }
  .poemkit-display-inline-flex--lg {
    display: inline-flex !important;
  }
}

@media all and (max-width: 1200px) {
  .poemkit-display-none--xl {
    display: none !important;
  }
  .poemkit-display-inline--xl {
    display: inline !important;
  }
  .poemkit-display-inline-block--xl {
    display: inline-block !important;
  }
  .poemkit-display-block--xl {
    display: block !important;
  }
  .poemkit-display-flex--xl {
    display: flex !important;
  }
  .poemkit-display-inline-flex--xl {
    display: inline-flex !important;
  }
}

/* 
 ---------------------------
 2. Elements after a floating element will flow around it. 
    Use the "poemkit-clearfix" hack to fix the problem
 ---------------------------
 */
.poemkit-clearfix::before {
  display: table;
  content: "";
  line-height: 0;
}

.poemkit-clearfix::after {
  display: table;
  content: "";
  line-height: 0;
  clear: both;
}

/* 
 ---------------------------
 3. Shadow Effect
 ---------------------------
 */
.poemkit-outer-shadow--regular {
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(50, 50, 93, 0.1) 0px 15px 35px 0px;
}

.poemkit-outer-shadow--thick {
  box-shadow: rgba(0, 0, 0, 0.18) 0 19px 38px, rgba(0, 0, 0, 0.08) 0 15px 12px;
}

.poemkit-outer-shadow--light {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
}

.poemkit-outer-shadow--highlight {
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(107, 107, 246, 0.2) 0px 15px 35px 0px;
}

.poemkit-none-shadow {
  box-shadow: none !important;
  text-shadow: none !important;
}

/* 
 ---------------------------
 4. Smooth Transition Effect
 ---------------------------
 */
.poemkit-trans {
  transition: all 0.3s ease;
}

.poemkit-trans img,
.poemkit-trans a,
.poemkit-trans i,
.poemkit-trans li,
.poemkit-trans span {
  transition: all 0.3s ease;
}

/* 
 ---------------------------
 5. Element Floating
 ---------------------------
 */
.poemkit-f-l {
  float: left;
}

.poemkit-f-r {
  float: right;
}

/* 
 ---------------------------
 6. Element Alignment
 ---------------------------
 */
.poemkit-t-r {
  text-align: right;
}

.poemkit-t-l {
  text-align: left;
}

.poemkit-t-c {
  text-align: center;
}

/* The breakpoint is the same as bootstrap 4 */
/*
--breakpoint-xs:0;
--breakpoint-sm:576px;
--breakpoint-md:768px;
--breakpoint-lg:992px;
--breakpoint-xl:1200px;
*/
@media all and (max-width: 576px) {
  .poemkit-t-r--sm {
    text-align: right;
  }
  .poemkit-t-l--sm {
    text-align: left;
  }
  .poemkit-t-c--sm {
    text-align: center;
  }
}

@media all and (max-width: 768px) {
  .poemkit-t-r--md {
    text-align: right;
  }
  .poemkit-t-l--md {
    text-align: left;
  }
  .poemkit-t-c--md {
    text-align: center;
  }
}

@media all and (max-width: 992px) {
  .poemkit-t-r--lg {
    text-align: right;
  }
  .poemkit-t-l--lg {
    text-align: left;
  }
  .poemkit-t-c--lg {
    text-align: center;
  }
}

@media all and (max-width: 1200px) {
  .poemkit-t-r--xl {
    text-align: right;
  }
  .poemkit-t-l--xl {
    text-align: left;
  }
  .poemkit-t-c--xl {
    text-align: center;
  }
}

/* 
 ---------------------------
 7. Truncate String with Ellipsis
 ---------------------------
 */
.poemkit-t-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  white-space: nowrap;
}

/* 
 ---------------------------
 8. Vertically or Horizontally Aligned Blocks
 ---------------------------
 */
/*-- Using transform property --*/
.poemkit-v-align--absolute,
.poemkit-v-align--relative {
  position: absolute;
  z-index: 6;
  width: 100%;
  /* Vertical Center */
  top: 50%;
  transform: translateY(-50%);
  /* Vertical Bottom */
  /* Vertical Top */
}

.poemkit-v-align--absolute.poemkit-v-align--absolute--b,
.poemkit-v-align--relative.poemkit-v-align--absolute--b {
  top: auto;
  bottom: 0;
  transform: translateY(0);
}

.poemkit-v-align--absolute.poemkit-v-align--absolute--t,
.poemkit-v-align--relative.poemkit-v-align--absolute--t {
  top: 0;
  transform: translateY(0);
}

.poemkit-v-align--relative {
  position: relative;
}

@media all and (max-width: 768px) {
  .poemkit-v-align--relative {
    top: 0;
    transform: none;
  }
}

/*-- Using table --*/
.poemkit-v-align--table {
  display: table;
  width: 100%;
  height: 100%;
  /* Vertical Bottom */
  /* Vertical Top */
}

.poemkit-v-align--table > div {
  display: table-cell;
  vertical-align: middle;
}

.poemkit-v-align--table.poemkit-v-align--table--b > div {
  vertical-align: bottom;
}

.poemkit-v-align--table.poemkit-v-align--table--t > div {
  vertical-align: top;
}

/* 
 ---------------------------
 9. Specifies Whether To Clip Content
 ---------------------------
 */
.poemkit-hidden-scrollbar-x {
  overflow-x: hidden;
}

.poemkit-hidden-scrollbar {
  overflow: hidden;
}

/* 
 ---------------------------
 10. Specifies a Default Height
 ---------------------------
 */
.poemkit-height--10 {
  height: 10vh !important;
}

.poemkit-height--20 {
  height: 20vh !important;
}

.poemkit-height--30 {
  height: 30vh !important;
}

.poemkit-height--40 {
  height: 40vh !important;
}

.poemkit-height--50 {
  height: 50vh !important;
}

.poemkit-height--60 {
  height: 60vh !important;
}

.poemkit-height--70 {
  height: 70vh !important;
}

.poemkit-height--80 {
  height: 80vh !important;
}

.poemkit-height--90 {
  height: 90vh !important;
}

.poemkit-height--100 {
  height: 100vh !important;
}

@media all and (max-width: 768px) {
  .poemkit-height--100:not(.is-mobile-still),
  .poemkit-height--90:not(.is-mobile-still),
  .poemkit-height--80:not(.is-mobile-still),
  .poemkit-height--70:not(.is-mobile-still),
  .poemkit-height--60:not(.is-mobile-still),
  .poemkit-height--50:not(.is-mobile-still),
  .poemkit-height--40:not(.is-mobile-still),
  .poemkit-height--30:not(.is-mobile-still),
  .poemkit-height--20:not(.is-mobile-still),
  .poemkit-height--10:not(.is-mobile-still) {
    height: auto !important;
  }
}

/* 
 ---------------------------
 11. Hidden The Element on Mobile or Desktop Device
 ---------------------------
 */
@media all and (max-width: 768px) {
  .poemkit-hide-mobile {
    display: none !important;
  }
}

@media all and (min-width: 769px) {
  .poemkit-hide-pc {
    display: none !important;
  }
}

/* 
 ---------------------------
 12. Tip Bubble
 ---------------------------
 */
.poemkit-bubble {
  font-weight: 100;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1.2;
  background: #6B6BF6;
  border-radius: 10px;
  padding: .1rem .3rem !important;
  margin-left: .5rem;
  font-size: 0.625rem;
  vertical-align: middle;
  color: #fff !important;
  position: absolute;
  right: 2.3rem;
  top: .5rem;
  transform: scale(0.7);
}

@media all and (max-width: 768px) {
  .poemkit-bubble {
    right: 1rem;
    top: .7rem;
  }
}

/* 
 ---------------------------
 13. Image Hover Overlay Effects
 ---------------------------
 */
img.poemkit-filter-hover--color,
.poemkit-filter-hover--color img {
  transition: filter 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  will-change: filter;
}

img.poemkit-filter-hover--color:hover,
.poemkit-filter-hover--color img:hover {
  filter: brightness(120%) hue-rotate(45deg);
}

/* 
 ---------------------------
 14. Position
 ---------------------------
 */
/*--- Directions --- */
[class*='poemkit-dir--top'],
[class*='poemkit-dir--bottom'],
[class*='poemkit-dir--left'],
[class*='poemkit-dir--right'],
[class*='poemkit-dir--center'] {
  position: absolute !important;
}

/* Don't use `width: 100%` because it is wrong if the parent has padding. */
.poemkit-dir--top {
  top: 0 !important;
  bottom: auto !important;
  left: 0 !important;
  right: 0 !important;
}

.poemkit-dir--bottom {
  top: auto !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
}

.poemkit-dir--left {
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: auto !important;
}

.poemkit-dir--right {
  top: 0 !important;
  bottom: 0 !important;
  left: auto !important;
  right: 0 !important;
}

.poemkit-dir--top-left {
  top: 0 !important;
  bottom: auto !important;
  left: 0 !important;
  right: auto !important;
}

.poemkit-dir--top-right {
  top: 0 !important;
  bottom: auto !important;
  left: auto !important;
  right: 0 !important;
}

.poemkit-dir--bottom-left {
  top: auto !important;
  bottom: 0 !important;
  left: 0 !important;
  right: auto !important;
}

.poemkit-dir--bottom-right {
  top: auto !important;
  bottom: 0 !important;
  left: auto !important;
  right: 0 !important;
}

/* 
 ---------------------------
 15. Element Hidden Style
 ---------------------------
 */
.poemkit-el--transparent {
  opacity: 0;
}

.poemkit-el--scale {
  transform: scale(0);
}

.poemkit-el--skew {
  transform: skew(0deg, -5deg);
}

/* 
 ---------------------------
  16. Border Radius
 ---------------------------
 */
.poemkit-border--rounded {
  border-radius: 4px;
}

.poemkit-border--rounded.poemkit-border--rounded-img > img {
  border-radius: 4px;
}

.poemkit-border--rounded.poemkit-border--rounded-only-img {
  border-radius: 0;
}

.poemkit-border--rounded.poemkit-border--rounded-only-img > img {
  border-radius: 4px;
}

.poemkit-border--circle {
  border-radius: 50%;
}

.poemkit-border--circle.poemkit-border--circle-img > img {
  border-radius: 50%;
}

.poemkit-border--circle.poemkit-border--circle-only-img {
  border-radius: 0;
}

.poemkit-border--circle.poemkit-border--circle-only-img > img {
  border-radius: 50%;
}

/* 
 ---------------------------
 17. Align Wide and Full Classes For Elements
 ---------------------------
 */
img.poemkit-alignfull,
.poemkit-alignfull img {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: 100vw !important;
  height: auto;
}

/* 
 ---------------------------
 18. HTML Disclosure Summary element 
 ---------------------------
 */
details.poemkit-details-reset:not([open]) > *:not(summary) {
  display: none !important;
}

details.poemkit-details-reset > summary {
  cursor: pointer;
  list-style: none;
}

details.poemkit-details-reset > summary::before {
  display: none;
}

details.poemkit-details-reset > summary::-webkit-details-marker {
  display: none;
}

/* ====================================================== 
   <!-- Basic Layout Styles -->                                        
/* ====================================================== */
/*
 * 1. Bootstrap 4.x                              - Do not use prefix "poemkit-"
 * 2. Material Container                         
 * 3. Main Wrapper                          
*/
/* 
 ---------------------------
 1. Bootstrap 4.x
 ---------------------------
 */
