.container {
  width: auto;
  max-width: 1140px;
}

@media all and (min-width: 1430px) {
  .container {
    max-width: 1278px;
  }
}

/* 
 ---------------------------
 2. Material Container
 ---------------------------
 */
.poemkit-container__bg {
  position: relative;
  z-index: 2;
  overflow: hidden;
}

.poemkit-container__bg .row {
  margin-left: 0;
  margin-right: 0;
}

.poemkit-container__bg.poemkit-container__bg--no-gutters {
  padding-left: 0;
  padding-right: 0;
}

.poemkit-container__bg.poemkit-container__bg--white {
  background: #fff;
}

.poemkit-container__bg.poemkit-container__bg--shadow {
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(50, 50, 93, 0.1) 0px 15px 35px 0px;
}

.poemkit-container__bg.poemkit-container__bg--rounded-large {
  border-radius: 15px;
}

.poemkit-container__bg.poemkit-container__bg--rounded-medium {
  border-radius: 7px;
}

.poemkit-container__bg.poemkit-container__bg--rounded-small {
  border-radius: 2px;
}

.poemkit-container__bg.poemkit-container__bg--totop-large {
  margin-top: -15rem;
}

.poemkit-container__bg.poemkit-container__bg--totop-medium {
  margin-top: -10rem;
}

.poemkit-container__bg.poemkit-container__bg--totop-small {
  margin-top: -5rem;
}

@media all and (max-width: 768px) {
  .poemkit-container__bg.poemkit-container__bg--totop-large {
