/* ====================================================== 
   <!-- Loader -->                                               
/* ====================================================== */
.poemkit-loader-progress {
  text-align: center;
  font-size: 0.75rem;
  transform: translate(10px, -10px);
}

.poemkit-loader-progress__line {
  top: 0;
  left: 0;
  width: 0;
  height: 4px;
  position: fixed;
  background-color: #fff;
  -webkit-touch-callout: none;
  user-select: none;
  pointer-events: none;
  z-index: 99999;
  box-shadow: 0 1px 5px 0 rgba(255, 255, 255, 0.3);
}

.poemkit-loader,
.poemkit-loader-progress {
  bottom: 0;
  left: 0;
  width: 65px;
  height: 65px;
  position: fixed;
  background-color: transparent;
  -webkit-touch-callout: none;
  user-select: none;
  pointer-events: none;
  /* Likely future */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
}

.poemkit-loader svg,
.poemkit-loader-progress svg {
  width: 65px;
  height: 65px;
}

.poemkit-loader svg path,
.poemkit-loader-progress svg path {
  fill: #6B6BF6;
}

.poemkit-loader span,
.poemkit-loader-progress span {
  font-family: Arial, sans-serif;
  position: fixed;
  z-index: 100000;
  bottom: 10px;
  left: 10px;
}

.poemkit-loader .poemkit-loader__spinner,
.poemkit-loader-progress .poemkit-loader__spinner {
  animation: poemkit-cssAnim--rotate .5s linear infinite;
}

.poemkit-loader .poemkit-loader__spinner path,
.poemkit-loader-progress .poemkit-loader__spinner path {
  stroke-dasharray: 1,150;
  /* 1%, 101% circumference */
  stroke-dashoffset: 0;
  stroke: #6B6BF6;
  stroke-linecap: round;
  animation: poemkit-cssAnim--dash 1.5s ease-in-out infinite;
}

@keyframes poemkit-cssAnim--rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes poemkit-cssAnim--dash {
  0% {
    stroke-dasharray: 1,150;
    /* 1%, 101% circumference */
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90,150;
    /* 70%, 101% circumference */
    stroke-dashoffset: -35;
    /* 25% circumference */
  }
  100% {
    stroke-dasharray: 90,150;
    /* 70%, 101% circumference */
    stroke-dashoffset: -124;
    /* -99% circumference */
  }
}

