/* 
 ---------------------------
 1. Social Button
 ---------------------------
 */
.poemkit-social-btn {
  position: relative;
  border: 2px solid #212121;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  z-index: 1;
  color: #212121;
  padding: 0;
  margin: 0;
  /* white style */
}

.poemkit-social-btn.is-fill-white {
  background: #fff;
  color: #212121;
}

.poemkit-social-btn.poemkit-social-btn--no-border {
  border-color: transparent !important;
}

.poemkit-social-btn.poemkit-social-btn--thin {
  border-width: 1px;
}

.poemkit-social-btn.poemkit-social-btn--circle {
  border-radius: 50%;
}

.poemkit-social-btn.poemkit-social-btn--circle::after {
  border-radius: 50%;
}

.poemkit-social-btn.poemkit-social-btn--rounded {
  border-radius: 4px;
}

.poemkit-social-btn.poemkit-social-btn--rounded::after {
  border-radius: 4px;
}

.poemkit-social-btn.poemkit-social-btn--small {
  width: 2rem;
  height: 2rem;
  line-height: 1.875rem;
  margin-right: .5rem;
}

.poemkit-social-btn.poemkit-social-btn--small i {
  font-size: 1rem;
}

.poemkit-social-btn.poemkit-social-btn--medium {
  width: 2.875rem;
  height: 2.875rem;
  line-height: 2.875rem;
  margin-right: .75rem;
}

.poemkit-social-btn.poemkit-social-btn--medium i {
  font-size: 1.2rem;
}

.poemkit-social-btn.poemkit-social-btn--large {
  width: 3.75rem;
  height: 3.75rem;
  line-height: 3.75rem;
  margin-right: 1rem;
}

.poemkit-social-btn.poemkit-social-btn--large i {
  font-size: 1.5rem;
}

.poemkit-social-btn::after {
  transition: all 0.3s ease;
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  display: block;
  left: 0;
  top: 0;
  background-color: #212121;
  visibility: hidden;
  opacity: 0;
  transform: scale(0.5, 0.5);
  z-index: -1;
}

.poemkit-social-btn:hover i {
  color: #fff;
}

.poemkit-social-btn:hover::after {
  visibility: visible;
  opacity: 1;
  transform: scale(1, 1);
}

.poemkit-social-btn.poemkit-social-btn--white {
  border-color: #fff;
  color: #fff;
}

.poemkit-social-btn.poemkit-social-btn--white:hover i {
  color: #212121;
}

.poemkit-social-btn.poemkit-social-btn--white:hover::after {
  background-color: #fff;
}

