.poemkit-typo--color-white .poemkit-typo--h5,
.poemkit-typo--color-white .poemkit-typo--h6 {
  color: white;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
}

/* 
 ---------------------------
 5. Drop Cap
 ---------------------------
 */
.poemkit-typo--dropcap::first-letter,
.poemkit-typo--dropcap > p:first-of-type::first-letter {
  display: block;
  margin-right: 10px;
  float: left;
  font-size: 3.5rem;
  line-height: 1;
  margin-bottom: -2px;
}

/* 
 ---------------------------
 6. Vertical Text
 ---------------------------
 */
.poemkit-typo--vertical {
  /* Defines the orientation of the text characters in a line.  */
}

.poemkit-typo--vertical:not(.poemkit-typo--vertical-characters-line) {
  writing-mode: vertical-rl;
  transform: translateX(0.5rem);
  line-height: 0;
  /* Fix the problem of left-leaning text after rotation */
}

.poemkit-typo--vertical.poemkit-typo--vertical-characters-line {
  width: 1px;
  word-wrap: break-word;
  line-height: 1;
  white-space: pre-wrap;
  /* this is for displaying whitespaces including Firefox */
}
