/* ======================================================
   <!-- Vertical Menu -->
/* ====================================================== */
/*
 * 1. Primary Menu
 * 2. Sub Menu (Using WordPress default class name )
 * 3. Multi-level Sub Menu Position
 * 4. Mega/Dropdown Menu
 * 5. Dropdown Arrow
*/
.poemkit-v-menu__container:not(.is-mobile) {
  width: 250px;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  z-index: 2;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.4);
  /*
	 ---------------------------
	 1. Primary Menu
	 ---------------------------
	 */
  /*
	 ---------------------------
	 5. Dropdown Arrow
	 ---------------------------
	 */
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu {
  margin: 0;
  padding: 0;
  /*
		 ---------------------------
		 2. Sub Menu (Using WordPress default class name )
		 ---------------------------
		 */
  /*
		 ---------------------------
		 4. Mega/Dropdown Menu
		 ---------------------------
		 */
  /* Multi-column */
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li {
  transition: all 0.3s ease;
  position: relative;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  text-align: right;
  padding: 0 1rem;
  font-weight: 500;
  font-size: 0.75rem;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li a {
  padding: 1.25rem 0 1.25rem;
  display: block;
  color: #525252;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li a:hover {
  color: #000;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li .poemkit-bubble {
  right: 1rem;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu {
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  top: 0;
  min-width: 200px;
  height: auto;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.4);
  background: #fff;
  z-index: 3;
  left: 250px;
  /*
			 ---------------------------
			 3. Multi-level Sub Menu Position
			 ---------------------------
			 */
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul {
  left: 200px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul {
  left: 400px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul {
  left: 600px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul {
  left: 800px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul {
  left: 1000px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul ul {
  left: 1200px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul ul ul {
  left: 1400px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul ul ul ul {
  left: 1600px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul ul ul ul ul {
  left: 1800px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu ul ul ul ul ul ul ul ul ul ul {
  left: 2000px;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column .poemkit-menu__arrow-mega {
  display: none !important;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column span.multi-column-title {
  color: #b9b9b9;
  font-weight: 700;
  display: inline-block;
  cursor: default;
  text-align: left;
  display: block;
  padding: 1rem 1rem 0;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul {
  min-width: inherit;
  white-space: nowrap;
  display: none;
  padding: 0;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul ul {
  display: block;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul ul li {
  float: none;
  display: block;
  text-align: left;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul li {
  overflow: hidden;
  vertical-align: top;
  padding: 0;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul li ul {
  position: relative;
  border: none;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul li li {
  padding: 0 1rem;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li {
  display: inline-block;
  max-height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: rgba(0, 0, 0, 0);
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li::-webkit-scrollbar {
  width: 1px;
  background-color: rgba(0, 0, 0, 0);
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: rgba(0, 0, 0, 0.4);
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li ul {
  left: 0;
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li li {
  border-bottom-color: rgba(0, 0, 0, 0.07);
}

.poemkit-v-menu__container:not(.is-mobile) ul.poemkit-menu li.multi-column > ul > li .poemkit-menu__arrow {
  display: none;
}

.poemkit-v-menu__container:not(.is-mobile) .poemkit-menu__arrow {
  display: block;
  position: absolute;
  right: .5rem;
  top: 50%;
  background-color: #333;
  width: 3px;
  height: 3px;
  border-radius: 100%;
}
