/* ====================================================== 
   <!-- Navigation -->                                    
/* ====================================================== */
/*
 * 1. Primary Menu
 * 2. Sub Menu (Using WordPress default class name )
 * 3. Mega/Dropdown Menu 
 * 4. Dropdown Arrow
 * 5. Right Box
*/
.poemkit-menu__container:not(.is-mobile) {
  float: right;
  padding-top: 1.25rem;
  /*
	Do not use `position:relative`, it may cause the width calculation script 
	matching problem of the mega menu
	*/
  /* 
	 ---------------------------
	 1. Primary Menu
	 ---------------------------
	 */
  /* 
	 ---------------------------
	 3. Mega/Dropdown Menu 
	 ---------------------------
	 */
  /* Multi-column */
  /* 
	 ---------------------------
	 4. Dropdown Arrow
	 ---------------------------
	 */
  /* 
	 ---------------------------
	 5. Right Box
	 ---------------------------
	 */
}

.poemkit-menu__container:not(.is-mobile).is-fixed {
  padding-bottom: 0;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu {
  font-size: 0.75rem;
  line-height: 2.34375;
  float: left;
  margin: 0;
  /* Required*/
  font-size: 0;
  /*Fighting the Space Between Inline Block Elements*/
  /* 
		 ---------------------------
		 2. Sub Menu (Using WordPress default class name )
		 ---------------------------
		 */
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu li {
  position: relative;
  margin-bottom: 0;
  padding: 0 1.05rem;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu li a {
  display: block;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li {
  display: inline-block;
  margin-right: 0;
  padding-bottom: 1.421875rem;
  white-space: nowrap;
  /* Required*/
  font-size: 0.75rem;
  /*Fighting the Space Between Inline Block Elements*/
  /* Sync to WordPress activation class name ".current-menu-item" */
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li .poemkit-bubble {
  right: 1rem;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > a {
  text-transform: uppercase;
  font-weight: 500;
  color: #525252;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > a:hover {
  color: #000;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li.is-active > a, .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li.current-menu-item > a {
  color: #6B6BF6;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li ul {
  min-width: 220px;
  display: none;
  position: absolute;
  padding-bottom: 0.525rem;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li ul li {
  padding-left: 1.25rem;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > ul {
  padding-top: 2.625rem;
  opacity: 0;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > ul::before {
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(50, 50, 93, 0.1) 0px 15px 35px 0px;
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 0.75rem;
  border: 1.15px solid rgba(33, 33, 33, 0.1);
  background: #fff;
  border-radius: 8px;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu {
  top: 100%;
  left: 0;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu a {
  word-wrap: break-word;
  white-space: normal;
  /* Required*/
  line-height: 1.4;
  padding: 0.35rem 0;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu a:hover {
  color: #000;
  background: #F1F1F1;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li:not(.multi-column) > ul {
  left: 1.784375rem;
  /* arrow of sub menu, not mega menu */
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li:not(.multi-column) > ul::after {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 15px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #ECECE8;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li:not(.multi-column) > ul ul {
  top: 0;
  left: 100%;
  padding: 1.421875rem 0;
  margin-left: -0.071875rem;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(50, 50, 93, 0.1) 0px 15px 35px 0px;
  border: 1.15px solid rgba(33, 33, 33, 0.1);
  background: #fff;
  border-radius: 8px;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li:not(.multi-column) > ul ul ul {
  margin-left: 0;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column span.multi-column-title {
  text-transform: uppercase;
  font-weight: 700;
  color: #B9B9B9;
  display: inline-block;
  cursor: default;
  padding-left: 1.05rem;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul {
  width: auto;
  white-space: nowrap;
  display: none;
  padding: 0;
  /* add scrollbar to per column */
  max-height: calc(100vh - 50px);
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li {
  width: 270px;
  /* Required, JS script needs to listen on it for calculation */
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding-left: 0.75rem;
  /* add scrollbar to per column */
  max-height: calc(100vh - 50px - 100px);
  overflow-y: auto;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: #fff;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li::-webkit-scrollbar {
  width: 3px;
  background-color: #FAFAFA;
  transform: scaleY(0.8);
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #FAFAFA;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li .poemkit-bubble {
  right: 1.2rem;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li a:hover {
  color: #000;
  background: #F1F1F1;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li ul {
  position: relative;
  box-shadow: none;
  /* Prevent JS script from disturbing */
  margin-top: 0 !important;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul ul {
  display: block;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul ul li {
  float: none;
  display: block;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column .poemkit-menu__arrow-mega {
  content: '';
  display: none;
  position: absolute;
  top: 56px;
  left: 50%;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #ECECE8;
  opacity: 0;
  z-index: 1;
}

.poemkit-menu__container:not(.is-mobile) ul.poemkit-menu ul.sub-menu .poemkit-menu__arrow {
  background-color: #000;
  right: 2.159375rem;
  margin-top: 0.3125rem;
}

.poemkit-menu__container:not(.is-mobile) li.multi-column > ul li .poemkit-menu__arrow {
  display: none;
}

.poemkit-menu__container:not(.is-mobile) .poemkit-menu__arrow {
  display: block;
  position: absolute;
  right: 0.284375rem;
  top: 25%;
  background-color: #000;
  width: 3px;
  height: 3px;
  border-radius: 100%;
}

.poemkit-menu__container:not(.is-mobile) .poemkit-menu__right-box {
  float: right;
}

.poemkit-header__container--overlay .poemkit-menu__container:not(.is-mobile) {
  /* 
	 ---------------------------
	 1. Primary Menu
	 ---------------------------
	 */
}

.poemkit-header__container--overlay .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > a {
  color: #DEDEDD;
}

.poemkit-header__container--overlay .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li > a:hover {
  color: #fff;
}

.poemkit-header__container--overlay .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li.is-active > a, .poemkit-header__container--overlay .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li.current-menu-item > a {
  color: #fff;
}

@media all and (max-width: 1250px) {
  .poemkit-menu__container:not(.is-mobile) ul.poemkit-menu > li {
    padding-left: 0.875rem;
    padding-right: 0.875rem;
    margin-right: 0;
  }
}

@media all and (max-width: 768px) {
  .poemkit-menu__container:not(.is-mobile) {
    display: none;
  }
}
