.poemkit-controls__file-field-container .poemkit-controls__file-field-trigger {
  width: 400px;
  max-width: 100%;
  height: 100%;
  background: #FBFDFD;
  border-radius: 8px;
  position: absolute;
  z-index: 4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #CBCBCB;
  pointer-events: hover;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 5px 15px 0px, rgba(50, 50, 93, 0.1) 0px 15px 35px 0px;
}

