  height: 65%;
  text-align: center;
}

.poemkit-controls__file-field-container .poemkit-controls__file-field-trigger > div::after {
  display: inline-block;
  font-family: 'Font Awesome 5 Free';
  font-weight: 600;
  content: "\f093";
