  visibility: hidden;
  cursor: pointer;
}

.poemkit-controls__file-field-container .poemkit-controls__file-field-trigger > div input::before {
  content: attr(data-title);
  position: absolute;
  color: #CBCBCB;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  font-size: 1.2rem;
