/* ====================================================== 
   <!-- Mobile Menu --> 
/* ====================================================== */
/* Mobile Menu Toggle */
.poemkit-menu-mobile__toggle {
  transition: all 0.3s ease;
  position: absolute;
  display: none;
  width: 40px;
  height: 40px;
  top: 15px;
  right: 20px;
  z-index: 90002;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  border-radius: 100%;
  transition: .1s ease-in-out;
}

.poemkit-menu-mobile__mask {
  content: '';
  left: 0;
  top: 0;
  position: fixed;
  z-index: 90001;
  display: block;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  height: 100%;
  display: none;
}

.js-poemkit-menu-opened .poemkit-menu-mobile__mask {
  background: rgba(0, 0, 0, 0.7);
  display: block;
}

/* Show Toolbar when viewing site with WordPress */
.admin-bar .poemkit-menu-mobile__toggle {
  top: 0;
}

.admin-bar .poemkit-menu-mobile__toggle.is-fixed {
  top: 20px;
}

/* Admin bar becomes taller on smaller devices with WordPress */
@media all and (max-width: 782px) {
  .admin-bar .poemkit-menu-mobile__toggle.is-fixed {
    top: 0;
  }
}

@media all and (max-width: 600px) {
  .admin-bar .poemkit-menu-mobile__toggle.is-fixed {
    top: 20px;
  }
}

@media all and (max-width: 768px) {
  .poemkit-menu-mobile__toggle {
    display: block;
  }
}

.poemkit-menu-mobile__toggle span {
  display: block;
  position: absolute;
  top: 14px;
  left: 12px;
  width: 20px;
  height: 2px;
  background: #525252;
  transition: all 0.3s ease;
}

.poemkit-menu-mobile__toggle span:nth-child(2) {
  top: 19px;
}

.poemkit-menu-mobile__toggle span:nth-child(3) {
  top: 24px;
}

body.js-poemkit-menu-opened .poemkit-menu-mobile__toggle span {
  left: 16px;
}

body.js-poemkit-menu-opened .poemkit-menu-mobile__toggle span:first-child {
  transform: rotate(45deg) translate(0, 7px);
}

body.js-poemkit-menu-opened .poemkit-menu-mobile__toggle span:nth-child(2) {
  opacity: 0;
}

body.js-poemkit-menu-opened .poemkit-menu-mobile__toggle span:nth-child(3) {
  transform: rotate(-45deg) translate(0, -7px);
}

/* Mobile Menu Container */
.poemkit-menu__container.is-mobile {
  transition: all 0.3s ease;
  left: -300px;
  display: block;
  position: fixed;
  top: 0;
  height: 100%;
  z-index: 999999;
  width: 260px;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: "lucida grande", tahoma, verdana, arial, sans-serif;
  font-size: 15px;
  background: #f8f8f8;
  color: #212121;
  box-shadow: 0px 0px 37px 0px rgba(0, 0, 0, 0.1);
  /* Mega & Dropdown */
  /* Form Elements */
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner {
  padding: 0 0 15px;
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > p {
  margin-left: 15px;
  margin-right: 15px;
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > .poemkit-menu__right-box {
  padding-left: .5rem;
  /* Social button for white color */
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > .poemkit-menu__right-box a {
  display: inline-block;
  font-size: 1rem;
  margin-right: .3rem;
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > .poemkit-menu__right-box .poemkit-social-btn--white {
  border-color: #212121;
  color: #212121;
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > .poemkit-menu__right-box .poemkit-social-btn--white:hover i {
  color: #212121;
}

.poemkit-menu__container.is-mobile > .poemkit-menu__inner > .poemkit-menu__right-box .poemkit-social-btn--white:hover::after {
  background-color: #fff;
}

.poemkit-menu__container.is-mobile h1, .poemkit-menu__container.is-mobile h2, .poemkit-menu__container.is-mobile h3, .poemkit-menu__container.is-mobile h4, .poemkit-menu__container.is-mobile h5, .poemkit-menu__container.is-mobile h6 {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(#ffffff, #dfdfdf);
  font-size: 11px;
  font-weight: normal;
  padding: 0 15px;
  margin: 0 0 5px;
  color: #212121;
  line-height: 24px;
  box-shadow: 0 5px 5px 3px rgba(0, 0, 0, 0.2);
}

.poemkit-menu__container.is-mobile p {
  font-size: 13px;
  margin: 0 0 12px;
}

.poemkit-menu__container.is-mobile p a {
  color: rgba(51, 51, 51, 0.9);
}

.poemkit-menu__container.is-mobile > p {
  margin-left: 15px;
  margin-right: 15px;
}

.poemkit-menu__container.is-mobile ul {
  display: block;
  margin: 0 0 15px;
  padding: 0;
  border-top: 1px solid #dfdfdf;
  border-bottom: none;
}

.poemkit-menu__container.is-mobile ul li {
  display: block;
  margin: 0;
  line-height: 48px;
  border-top: none;
  border-bottom: 1px solid #dfdfdf;
  position: relative;
}

.poemkit-menu__container.is-mobile ul li a,
.poemkit-menu__container.is-mobile ul li span {
  padding: 0 15px;
  display: block;
  text-decoration: none;
  color: #212121;
}

.poemkit-menu__container.is-mobile ul li a:hover {
  color: #000;
}

.poemkit-menu__container.is-mobile ul li.is-active > a,
.poemkit-menu__container.is-mobile ul li.is-active > span {
  box-shadow: 0 0 15px 3px #ebebeb inset;
}

.poemkit-menu__container.is-mobile ul li ul {
  border-bottom: 0;
  margin: 0;
}

.poemkit-menu__container.is-mobile ul li ul li {
  line-height: 40px;
  font-size: 13px;
}

.poemkit-menu__container.is-mobile ul li ul li:last-child {
  border-bottom: 0;
}

.poemkit-menu__container.is-mobile ul li ul li a:hover {
  color: #000;
}

.poemkit-menu__container.is-mobile ul li ul li.is-active > a,
.poemkit-menu__container.is-mobile ul li ul li.is-active > span {
  box-shadow: 0 0 15px 3px #ebebeb inset;
}

.poemkit-menu__container.is-mobile ul li ul li a,
.poemkit-menu__container.is-mobile ul li ul li span {
  color: rgba(51, 51, 51, 0.8);
}

.poemkit-menu__container.is-mobile li > .sub-menu {
  margin-top: 0 !important;
  overflow: hidden;
  height: 0;
}

.poemkit-menu__container.is-mobile li > .sub-menu ul {
  height: auto;
}

.poemkit-menu__container.is-mobile li.multi-column ul {
  border: none;
}

.poemkit-menu__container.is-mobile li.multi-column > ul .multi-column-title {
  font-weight: bold;
  color: #ABABAB;
}

.poemkit-menu__container.is-mobile li:not(.multi-column) ul ul {
  padding-left: 0.721875rem;
}

.poemkit-menu__container.is-mobile .poemkit-menu__arrow-mobile {
  position: absolute;
  top: 0;
  right: 10px;
  font-style: normal;
  font-weight: 100;
  font-family: serif;
  color: #ABABAB;
}

.poemkit-menu__container.is-mobile .poemkit-menu__arrow-mobile::after {
  content: '+';
  display: inline-block;
  font-size: 1rem;
}

.poemkit-menu__container.is-mobile li.is-active .poemkit-menu__arrow-mobile::after {
  content: '-';
}

.poemkit-menu__container.is-mobile form {
  margin: 0 15px;
}

.poemkit-menu__container.is-mobile label {
  font-size: 13px;
}

.poemkit-menu__container.is-mobile input:not([type="checkbox"]):not([type="radio"]) {
  width: 100%;
  font-size: 13px;
  padding: 5px;
  box-sizing: border-box;
  margin: 0 0 10px;
  border-radius: 2px;
  border: 0;
  background: rgba(0, 0, 0, 0.1);
  color: rgba(51, 51, 51, 0.6);
  display: block;
  clear: both;
}

.poemkit-menu__container.is-mobile textarea,
.poemkit-menu__container.is-mobile select {
  width: 100%;
  font-size: 13px;
  padding: 5px;
  box-sizing: border-box;
  margin: 0 0 10px;
  border-radius: 2px;
  border: 0;
  background: rgba(0, 0, 0, 0.1);
  color: rgba(51, 51, 51, 0.6);
  display: block;
  clear: both;
}

.poemkit-menu__container.is-mobile input[type="checkbox"] {
  width: auto;
  display: inline;
  clear: none;
}

.poemkit-menu__container.is-mobile input[type="button"], .poemkit-menu__container.is-mobile input[type="submit"] {
  color: #f8f8f8;
  background: #212121;
}

.poemkit-menu__container.is-mobile input[type="button"]:hover, .poemkit-menu__container.is-mobile input[type="submit"]:hover {
  background: rgba(51, 51, 51, 0.9);
}

.js-poemkit-menu-opened .poemkit-menu__container.is-mobile {
  left: 0;
}
