/* ====================================================== 
   <!-- Header Area -->                                        
/* ====================================================== */
/*
 * 1. Default
 * 2. Theme - White
*/
/* 
 ---------------------------
 1. Default
 ---------------------------
 */
/* Don't use "margin" or "padding" to set up ".poemkit-header__container" */
.poemkit-header__container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 90000;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  transition: .1s ease-in-out;
  background-color: #fff;
  /* 
	 ---------------------------
	 2. Overlay Header
	 ---------------------------
	 */
}

.poemkit-header__container.poemkit-header__container--overlay {
  background: rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.poemkit-header__container.poemkit-header__container--overlay.is-fixed {
  background-color: #fff;
}

/* Show Toolbar when viewing site with WordPress */
.admin-bar .poemkit-header__container {
  top: 32px;
}

/* Admin bar becomes taller on smaller devices with WordPress */
@media all and (max-width: 782px) {
  .admin-bar .poemkit-header__container {
    top: 0;
  }
}

/* Don't use "margin" or "padding" to set up ".poemkit-header" */
.poemkit-header {
  /* Floating layer protection for IE */
  background: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
}

/* Header Placeholder, to prevent floating elements from affecting layout */
.poemkit-header__placeholder {
  height: 75px;
  background: none;
  display: block;
  visibility: hidden;
  width: 100%;
}

@media all and (max-width: 768px) {
  .poemkit-header__container {
    position: relative;
  }
  .poemkit-header__container.poemkit-header__container--overlay {
    background-color: #6B6BF6;
    background-image: linear-gradient(to right, #6B6BF6 0%, #5757E9 20%, #7D7DFC 100%);
    position: relative;
  }
  /* Header Placeholder, to prevent floating elements from affecting layout */
  .poemkit-header__placeholder {
    height: 0;
  }
}
